import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import VueRouter from 'vue-router';
import routes from './router'
import store from './store'
import './public-path';


Vue.config.productionTip = false

console.log("*****************子项目测试window*****************")
console.log(window.huangyue)
let router = null;
let instance = null;

function render(state) {
  router = new VueRouter({
    base: window.__POWERED_BY_QIANKUN__ ? '/qiankun_child1' : '/',
    mode: 'history',
    routes,
  });

  instance = new Vue({
    router,
    store,
    // render: h => h(App),
    render: h => h(App, {
        props: {...state }
    })
  }).$mount('#app');
}

window.huangyue = {
  setNane:'sssd'
}
if (!window.__POWERED_BY_QIANKUN__) {
  render();
}
//测试全局变量污染
// window.a = 1;
export async function bootstrap(data) {
  console.log("***************进入child1项目bootstrap******************")
  console.log(data);
}
export async function mount(props) {
  Vue.use(props.data.components)
  // 挂载主项目传递过来的全局方法
  Object.keys(props.data.libraryJs).forEach(i => {
    Vue.prototype[i] = props.data.libraryJs[i]
  });
  // Vue.prototype.$tools = props.data.tools
  console.log("***************进入child1项目mount******************")
  console.log(props);
  render(props.data.store);
  // 测试一下 body 的事件，不会被沙箱移除
  // document.body.addEventListener('click', e => console.log('document.body.addEventListener'))
  // document.body.onclick = e => console.log('document.body.addEventListener')
}

export async function unmount() {
  instance.$destroy();
  instance = null;
  router = null;
}


/**
 * bootstrap 只会在子应用初始化的时候调用一次，下次子应用重新进入时会直接调用 mount 钩子，不会再重复触发 bootstrap。
 * 通常我们可以在这里做一些全局变量的初始化，比如不会在 unmount 阶段被销毁的应用级别的缓存等。
 */
